.project{display:flex;align-items:center;justify-content:flex-start;height:100%;flex-direction:column}.project.center{justify-content:center}.project .details{background-color:#eeeeee;width:100%;text-align:left;box-shadow:6px 6px black}.project .details .item-content{margin:10px}.project .details:active{box-shadow:4px 4px gray}.project .center-item{text-align:center;color:#666;width:100%;max-width:80%}.project .center-item .input-element{width:80%;margin:0 auto}.project .error{color:white;background-color:red;font-weight:bold;opacity:0.4;height:15px;font-size:0.9rem;margin:10px 0px;padding:3px;border-radius:5px}.project .error:empty{background-color:transparent}
