body,html{font-family:monospace;height:100%;margin:0;padding:0;width:100%}#app{margin:0;padding:0;height:100%}.content{height:100%;display:flex;flex-flow:column;max-width:500px;margin:0 auto}.content .main{flex:1 1 auto;padding:10px 15px;white-space:nowrap;overflow:auto}.content .main>div{white-space:pre-wrap}h1{font-size:2rem;color:red;text-align:center}h2{font-size:1.5rem;color:#aaa;text-align:left;border-bottom:1px solid #aaa;padding:5px 0px;width:100%;margin:0}h3{font-size:1.3rem}h4{margin:5px}button,.button{font-family:monospace;margin:10px auto;display:block;background-color:#525252;color:#ffffff;padding:5px 10px;font-weight:bold;border-radius:5px;font-size:1rem;width:200px;border:none;text-align:center}button:disabled,.button:disabled{background-color:#ddd}a{text-decoration:none;color:#000000}p{max-width:500px}
